@import "../../global.scss";

.utility {
  //position: absolute;
  width: 100%;
  height: 200px;
  background-color: $btn-color;

  z-index: 1;
  padding: 0 20rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  div{
    display: flex;
    align-items: center;
    img{
      margin-right: 15rem;
     
    }
  }
   @include respond(laptop);@include respond(desktop) {
        width: 100vw;
         padding:  0 5rem;
      }
  @include respond (mobile); @include respond (tablet) {
    flex-direction: column;
    justify-content: space-evenly;
    width: 90vw;
    height: 150px;
    padding: 0 2rem;
    div {
      display: flex;
      align-items: center;
      img {
        margin-right: 2rem;
      }
    }
  }
  img {
    width: 150px;
    height: 150px;
    border-radius: 70%;
    @include respond (mobile); @include respond (tablet) {
      width: 50px;
      height: 50px;
    }
  }
  h1 {
    font-size: 4rem;
    color: #fff;
    @include respond (laptop){
      font-size: 3vw;
    }
    @include respond (mobile); @include respond (tablet){
      font-size: 4vw;
    }
  }
  button {
    background-color: #fff;
    color: #000;
  }
}
